
import { defineComponent, ref, reactive, toRefs, onMounted, toRaw } from "vue";
import { useRouter } from "vue-router";
import { Toast } from "vant";
export default defineComponent({
  name: "Index",
  setup() {
    let data = reactive({
      notesList: [],
      sourceList: [],
      TimeParameter: 10,
      integralBox: false,
      integral: 15,
      width: 22,
    });
    // 获取路由参数
    const router = useRouter().currentRoute.value.query;
    console.log(router);
    if (router.token) {
      localStorage.setItem("Gk-Read-Token", router.token as any);
    }
    const bookInfo = {
      bookPath:
        (router.bookPath as any) ||
        // "https://yufengtuchuang.oss-cn-beijing.aliyuncs.com/file/9787227071143_2.epub",
        "https://ouchn-prod-public.oss-cn-beijing.aliyuncs.com/goods_file/beea6394-0553-4b53-8738-6ad2847f187f.epub",
      // "https://ouchn-prod-public.oss-cn-beijing.aliyuncs.com/goods_file/51c80e60-ac57-46d2-ad63-65e467a50f06.epub",
      // "https://ouchn-dev-public.oss-cn-qingdao.aliyuncs.com/goods_file/dae840c2-337c-4fc2-b6a3-eedc5dea085c.epub",
      // "https://ouchn-prod-public.oss-cn-beijing.aliyuncs.com/goods_file/fef8e6b9-503d-4aff-a199-c34c3e60fc13.epub",
      // "https://gateway.ysy.crtvup.com.cn/ouchn-source/goodsFile/downloadEncryptFile/1562769824491331586.epub?key=41729bb7-f633-4a57-9c51-f6f0f84d5589",
      // "https://gateway.ysy.crtvup.com.cn/ouchn-source/goodsFile/downloadEncryptFile/1562771181319315458.epub?key=f939f92a-64af-4b0f-b7cf-81e3f4e56c0d",
      // "/123.epub",
      // "/e123.epub",

      location: (router.location as any) || "0",
      fileType: (router.fileType as any) || "epub",
      goodsFileId: (router.goodsFileId as any) || "",
      goodsId: (router.goodsId as any) || "",
      relevanceId: (router.relevanceId as any) || "",
      resource: (router.resource as any) || "",
      resourceType: (router.resourceType as any) || "",
      teachInstId: (router.teachInstId as any) || "",
      limitSize: Number(router.limitSize as any) || 100, //限制阅读内容大小
      innerHeight: Number(router.innerHeight as any) || window.innerHeight,
      innerWidth: Number(router.innerWidth as any) || window.innerWidth,
      luminance: (router.luminance as any) || "luminance", //是否显示亮度
      isNote: (router.isNote as any) || "no", //是否可以添加笔记
      flow: (router.flow as any) || "scrolled",
    };

    onMounted(() => {});
    return {
      data,
      bookInfo,
      ...toRefs(data),
    };
  },
});
